@import "../presets.scss";

//preset transition for input field webkits
@mixin social {
  transition: color 0.3s ease;
}
//Desktop styling
.container {
  display: flex;
  padding: 0 0.5rem;
  text-align: center;
  margin-bottom: 5rem;
  flex-direction: column;
  align-items: center;

  a {
    text-decoration: none;
    color: $clr-nutral-1000;
  }

  h1 {
    text-transform: uppercase;
  }

  //Quick access contact cards styling
  .contactCards {
    display: grid;
    gap: 2rem;
    margin-top: 3rem;
    margin-bottom: 2rem;
    grid-template-columns: repeat(3, 20rem);

    .card {
      padding: 0.5rem 1rem;
      background-color: $clr-nutral-0;
      border-radius: $radius-sm;
      display: flex;
      flex-direction: column;
      align-items: center;
      box-shadow: 0px 0px 64px 0px rgba(0, 0, 0, 0.1);
      transition: 200ms ease-in-out;

      &:hover {
        background-color: $clr-accent-400;
        color: $clr-nutral-0;
        outline: 2px solid white;
        transition: 300ms ease;
        border: none;
      }

      h3 {
        font-size: $fs-600;
      }
      img {
        height: 2.8rem;
        margin-bottom: 0.5rem;
      }
      p {
        margin-bottom: 0.5rem;
        font-size: $fs-200;
      }
      .cardIcon {
        font-size: 30px;
        margin-bottom: -10px;
        margin-top: 10px;
      }
    }
  }

  //Contact form styling
  .contactForm {
    background-color: $clr-nutral-0;
    border-radius: $radius-sm;
    position: relative;
    box-shadow: 0px 0px 64px 0px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px 100px;
    // width: 700px;
    // height: 630px;

    //Contact form "formsvg"  styling
    .formSVG {
      position: absolute;
      width: 420px;
      bottom: 0;
      left: -150px;
    }

    .form {
      text-align: left;
      display: flex;
      align-items: center;
      flex-direction: column;

      //Submit button styling
      .formButton {
        margin-top: 25px;
        height: 2.5rem;
        width: 10.5rem;
        border-radius: $radius-sm;
        background: $clr-accent-400;
        font-weight: $fw-bold;
        color: $clr-nutral-0;
        border: 2px solid $clr-accent-400;
        z-index: 1;
        // box-shadow: 0 0 5px $clr-nutral-700;
        transition: 200ms ease-in-out;

        &:hover {
          background-color: $clr-nutral-0;
          color: $clr-nutral-1000;
          transition: 200ms ease-in-out;
          border: 2px solid $clr-accent-400;
          // box-shadow: 0 0 5px $clr-accent-400;
          cursor: pointer;
        }
      }

      //Input field styling
      .inputField {
        z-index: 1;
        margin-trim: 10px;

        .errorContainer {
          padding-bottom: 15px;

          .errorMsg {
            font-size: 12px;
            color: red;
            margin-top: 2px;
          }
        }
        &:focus-within {
          label {
            color: $clr-nutral-1000;
          }
        }
        .inputIconContainer {
          margin-top: 5px;
          position: relative;
          .formIcon {
            position: absolute;
            transform: translate(12px, 12px);
            font-size: 27px;
            pointer-events: none;
          }

          input:-webkit-autofill {
            -webkit-background-clip: text;
          }

          input {
            // display: flex;
            border: 2px solid;
            width: 430px;
            height: 50px;
            padding-left: 55px;
            margin-bottom: 2px;
            // background-color: $clr-nutral-0;
            border-radius: $radius-sm;
            font-size: $fs-200;
            box-shadow: 0 0 5px transparent;
            transition: 200ms ease-in-out;

            &:focus {
              outline: none;
              border: 1px solid $clr-nutral-1000;
              box-shadow: 0 0 5px $clr-nutral-1000;
              transition: all 0.4s ease;
            }

            &:hover {
              transition: 200ms ease;
              border: 2px solid transparent;
              box-shadow: 0 0 5px $clr-nutral-1000;
            }
          }

          //textarea field styling
          textarea {
            height: 180px;
            border: 2px solid;
            width: 430px;
            padding-left: 55px;
            padding-top: 15px;
            background-color: $clr-nutral-0;
            border-radius: $radius-sm;
            font-size: $fs-200;
            resize: none;
            transition: 200ms ease-in-out;

            &:focus {
              outline: none;
              border: 1px solid $clr-nutral-1000;
              box-shadow: 0 0 5px $clr-nutral-1000;
              transition: all 0.4s ease;
            }

            &:hover {
              transition: 200ms ease;
              outline: none;
              border: 2px solid transparent;
              box-shadow: 0 0 5px $clr-nutral-1000;
            }
          }
        }
        label {
          font-size: $fs-300;
          text-align: left;
          font-weight: $fw-medium;
        }
        p {
          text-align: left;
        }
      }
    }
  }

  //Mobile version stylinh
  @media (max-width: 66.25em) {
    .contactCards {
      grid-template-columns: repeat(1, 30rem);
      .card {
        height: 15rem;
        justify-content: center;
      }
    }

    .contactForm {
      .formSVG {
        width: 400px;
        left: -120px;
      }
      .form {
        .formButton {
          width: 190px;
          height: 42px;
        }
        .inputField {
          .inputIconContainer {
            .formIcon {
              display: flex;
              transform: translate(13px, 13px);
              font-size: 30px;
            }
            input {
              width: 450px;
              height: 55px;

              font-size: $fs-300;
            }
            textarea {
              height: 200px;
              width: 450px;
              font-size: $fs-300;
            }

            label {
              font-size: $fs-300;
              // transform: translate(-220px, -2px);
              font-weight: $fw-medium;
            }
          }
        }
      }
    }
  }

  @media (max-width: 50em) {
    h1 {
      text-transform: uppercase;
      font-size: $fs-700;
    }
    h3 {
      font-size: $fs-400;
    }

    .contactForm {
      width: calc(100vw - 2rem);

      .formSVG {
        display: none;
      }
      .form {
        .inputField {
          .inputIconContainer {
            .formIcon {
              display: flex;
              transform: translate(13px, 13px);
              font-size: 25px;
            }
            input {
              width: 75vw;
              height: 50px;
              font-size: $fs-200;
            }
            textarea {
              height: 200px;
              width: 75vw;
              font-size: $fs-300;
            }

            label {
              font-size: $fs-300;
              // position: absolute;
              // transform: translate(-220px, -2px);
              font-weight: $fw-medium;
            }
          }
        }
      }
    }
  }

  @media (max-width: 36em) {
    .conatiner {
      padding: 0 2rem;
    }
    h1 {
      text-transform: uppercase;
      font-size: $fs-600;
    }
    h3 {
      font-size: $fs-300;
    }
    .contactCards {
      grid-template-columns: calc(100vw - 2rem);
    }

    .contactForm {
      width: calc(100vw - 2rem);
      // height: 750px;

      .formSVG {
        display: none;
      }
      .form {
        .formButton {
          width: 165px;
          height: 42px;
        }

        .inputField {
          .inputIconContainer {
            input {
              width: 75vw;
              height: 50px;
              font-size: $fs-200;
            }
            textarea {
              height: 200px;
              width: 75vw;
              font-size: $fs-300;
            }

            label {
              font-size: $fs-300;
              // position: absolute;
              // transform: translate(-35vw, -2px);
              font-weight: $fw-medium;
            }
          }
        }
      }
    }
  }
}
